*,
*::before,
*::after {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

html {
  font-size: 62.5%;
  overflow-x: hidden;
}

body {
  box-sizing: border-box;
  font-size: 1.6rem;
}

:root {
  --font-headings: Roie, sans-serif;
  --font-body: poppins, sans-serif;
  --color-primary: #00ae83;
  --color-primary--dark: #00644b;
  --color-primary--darkest: #2e3636;
  --color-grey: #4b4b4b;
  --color-grey--light: #707070;
  --color-spark-yellow: #f4c063;
  --transition--standard: 0.3s ease;
}

aside, section:where(:not(.hero)) {
  position: relative;
  padding: 6.5rem 0;
}

section {
  position: relative;
}
figure {
  margin-bottom: 0;
}

main {
  position: relative;
}

.container {
  position: relative;
}

.row {
  position: relative;
}

.col {
  position: relative;
  height: auto;
}

footer {
  margin-top: -1px;
}

body li:not(.text-content li), body p:where(:not(.text-content p)), body .text-content {
  font-family: "poppins", sans-serif;
  font-size: 1.35em;
}

ul {
  margin-left: 3rem;
}

body {
  color: black;
  font-family: "poppins", sans-serif;
}
body a {
  text-decoration: none;
}
body .small,
body small {
  font-family: "poppins", sans-serif;
}

.headline, h5,
.h5,
h6,
.h6, h4,
.h4, h3,
.h3, h2,
.h2 {
  font-family: "Roie", sans-serif;
  font-weight: 700;
}

h1,
.h1 {
  font-family: "Roie", sans-serif;
  font-size: 4em;
  text-transform: lowercase;
}

h2,
.h2 {
  font-size: 3em;
  color: #4b4b4b;
}

h3,
.h3 {
  font-size: 2.4em;
  text-transform: capitalize;
}

h4,
.h4 {
  font-size: 1.75em;
}

.white-stroke, .subheadline {
  font-family: "poppins", sans-serif;
  font-weight: 400;
  font-size: 1.35em;
}

.white-stroke {
  text-transform: uppercase;
  color: transparent;
  -webkit-text-stroke: 1px white;
  font-size: 3em;
  font-weight: 700;
}

.site-title {
  font-family: "Roie", sans-serif;
  text-transform: lowercase;
  letter-spacing: 1.3;
  font-size: 3rem;
}

.hide {
  display: none !important;
  visibility: hidden !important;
}

.bg-color-primary {
  background-color: var(--color-primary);
  color: white;
}

.bg-color-primary--dark {
  background-color: var(--color-primary--dark);
  color: white;
}

.bg-rolling-hills {
  background-repeat: no-repeat;
  background-size: 100%;
  height: 110rem;
}
.bg-rolling-hills .container {
  position: absolute;
  top: 20%;
  left: 5%;
}

.text-roie {
  font-family: var(--font-headings) !important;
  letter-spacing: 0.2em;
}

.clip-color-left > :nth-child(1) {
  clip-path: polygon(0 0, 100% 5%, 100% 95%, 0 100%);
}
.clip-color-left > :nth-child(2) {
  clip-path: polygon(0 5%, 100% 0, 100% 100%, 0 95%);
}
.clip-color-left > :nth-child(3) {
  clip-path: polygon(0 5%, 100% 0, 100% 100%, 0 95%);
}

.clip-color-right > :nth-child(1) {
  clip-path: polygon(0 5%, 100% 0, 100% 100%, 0 95%);
}
.clip-color-right > :nth-child(2) {
  clip-path: polygon(0 0, 100% 5%, 100% 95%, 0 100%);
}
.clip-color-right > :nth-child(3) {
  clip-path: polygon(0 0, 100% 5%, 100% 95%, 0 100%);
}

.clip-color-left-top > :nth-child(1) {
  clip-path: polygon(0 0, 100% 5%, 100% 100%, 0 100%);
}
.clip-color-left-top > :nth-child(2) {
  clip-path: polygon(0 5%, 100% 0, 100% 100%, 0 100%);
}
.clip-color-left-top > :nth-child(3) {
  clip-path: polygon(0 5%, 100% 0, 100% 100%, 0 100%);
}

.clip-color-zig-zag-left > :nth-child(1) {
  clip-path: polygon(0 0, 100% 5%, 100% 100%, 0 95%);
}
.clip-color-zig-zag-left > :nth-child(2) {
  clip-path: polygon(0 5%, 100% 0, 100% 95%, 0 100%);
}
.clip-color-zig-zag-left > :nth-child(3) {
  clip-path: polygon(0 5%, 100% 0, 100% 95%, 0 100%);
}

.clip-color-zig-zag-right > :nth-child(1) {
  clip-path: polygon(0 5%, 100% 0, 100% 95%, 0 100%);
}
.clip-color-zig-zag-right > :nth-child(2) {
  clip-path: polygon(0 0, 100% 5%, 100% 100%, 0 95%);
}
.clip-color-zig-zag-right > :nth-child(3) {
  clip-path: polygon(0 0, 100% 5%, 100% 100%, 0 95%);
}

.hamburger {
  width: 7rem;
  height: 50%;
  cursor: pointer;
}
.hamburger__lines {
  border: 2px solid var(--color-primary);
  display: block;
  border-radius: 1rem;
  width: 100%;
}

.pill-btn__outline--white, .pill-btn__fill--primary, .pill-btn, .btn, .btn__primary--fill, .menu-item.cta {
  border: 2px solid transparent;
  display: inline-block;
  font-size: 2rem;
  font-weight: 500;
  transform: translateY(0);
  text-transform: uppercase;
  transition: background-color 0.3s ease, border-color 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease, color 0.3s ease;
  width: auto;
}
.pill-btn__outline--white:hover, .pill-btn__fill--primary:hover, .pill-btn:hover, .btn:hover, .btn__primary--fill:hover, .menu-item.cta:hover {
  box-shadow: 2px 4px 8px rgba(0, 0, 0, 0.35);
  transform: translateY(-4px);
}

.pill-btn__outline--white, .pill-btn__fill--primary, .pill-btn, .btn, .btn__primary--fill, .menu-item.cta {
  border-radius: 999px;
  padding: 0.5em 1em;
}

.btn__primary--fill, .menu-item.cta {
  background-color: #00ae83;
  color: white;
}
.btn__primary--fill a, .menu-item.cta a {
  color: white;
}
.btn__primary--fill:hover, .menu-item.cta:hover, .btn__primary--fill:active, .menu-item.cta:active, .btn__primary--fill:focus, .menu-item.cta:focus {
  background-color: white;
  border-color: #00ae83;
  color: #00ae83;
}
.btn__primary--fill:hover a, .menu-item.cta:hover a, .btn__primary--fill:active a, .menu-item.cta:active a, .btn__primary--fill:focus a, .menu-item.cta:focus a {
  color: #00ae83;
}

a.btn, a.btn__primary--fill, a.menu-item.cta {
  text-decoration: none;
}

.pill-btn__fill--primary {
  color: white;
  background-color: #00ae83;
}
.pill-btn__outline--white {
  color: white;
  border-color: white;
  background-color: transparent;
}

#main-nav {
  display: none;
}

.navbar, .mobile-navbar {
  align-items: center;
}
.navbar a, .mobile-navbar a {
  transition: color 0.3s ease;
  text-decoration: none;
}
.navbar a:not(:has(h1)), .mobile-navbar a:not(:has(h1)) {
  text-transform: uppercase;
}
.navbar__menu {
  list-style: none;
  padding: unset;
}

.menu-item {
  padding: 10px;
  margin: 0 10px;
  display: inline-block;
  border-bottom: 2px solid transparent;
  transition: border-color 0.3s ease;
}
.menu-item a {
  color: white;
}
.menu-item:hover {
  border-color: white;
  cursor: pointer;
}
.menu-item:hover a {
  color: white;
}
.offcanvas {
  padding-top: 2rem;
  background-color: var(--color-primary--dark);
}
.offcanvas__header {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 1rem;
}
.offcanvas .logo-image svg {
  color: var(--color-primary);
}
.offcanvas .btn-close {
  color: white;
  font-size: 5rem;
  background: none;
  width: unset;
  height: unset;
  padding: unset;
  box-sizing: unset;
  opacity: 1;
}

.header {
  width: 100%;
  background-color: transparent;
  position: absolute;
  top: 0;
  z-index: 5;
  color: white;
}
.header .row {
  justify-content: space-between;
}
.header a {
  color: white;
}
.header__social-icon svg {
  width: 4rem;
  height: auto;
}

.footer .logo {
  color: white;
}

.footer-nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.footer-nav ul a {
  color: white;
}

#leaves-4 {
  stroke: white;
}
#leaves-4 .a,
#leaves-4 .c {
  fill: none;
}
#leaves-4 .b {
  clip-path: url(#a);
}
#leaves-4 .c {
  stroke: inherit;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-width: 6px;
}

#leaves-3 {
  transform: rotate(180deg);
  stroke: white;
}
#leaves-3 .cls-1,
#leaves-3 .cls-3 {
  fill: none;
}
#leaves-3 .cls-2 {
  clip-path: url(#clip-path);
}
#leaves-3 .cls-3 {
  stroke: inherit;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-width: 6px;
}

#icon-healthy-growth {
  fill: var(--color-primary);
}
#icon-healthy-growth .a {
  fill: none;
}
#icon-healthy-growth .b {
  clip-path: url(#icon-healthy-growth-a);
}
#icon-healthy-growth .c {
  fill: inherit;
}

.hero {
  position: relative;
  display: block;
  height: unset;
  padding-top: 30rem;
  padding-bottom: 4rem;
}
@media (min-width: 992px) {
  .hero {
    min-height: 80rem;
    padding-top: 15.6rem;
    padding-bottom: 4rem;
  }
}
.hero__background {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
}
.hero__background > :nth-child(1) {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1;
}
.hero__background > :nth-child(2) {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1;
  background-position: center;
  background-size: cover;
}
.hero__background > :nth-child(3) {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.75);
  z-index: 2;
}
.hero__background--color {
  background-color: var(--color-spark-yellow);
}
.hero__background--lower {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.hero__background--upper {
  background-color: rgba(0, 0, 0, 0.65);
}
.hero__background.color-right > :nth-child(1) {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 95%);
}
.hero__background.color-right > :nth-child(2) {
  clip-path: polygon(0 0, 100% 0, 100% 95%, 0% 100%);
}
.hero__background.color-right > :nth-child(3) {
  clip-path: polygon(0 0, 100% 0, 100% 95%, 0% 100%);
}
.hero__background.color-left > :nth-child(1) {
  clip-path: polygon(0 0, 100% 0, 100% 95%, 0% 100%);
}
.hero__background.color-left > :nth-child(2) {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 95%);
}
.hero__background.color-left > :nth-child(3) {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 95%);
}
.hero__content {
  z-index: 4;
}
.hero__content #leaves-3 {
  transform: rotate(150deg);
  stroke: white;
  position: absolute;
  top: -5rem;
  left: -1rem;
  width: 100%;
}
.hero__content--headline {
  color: white;
  font-family: "Roie", sans-serif;
}
.hero__content--subheadline {
  font-size: 3rem;
  color: white;
}
.hero__content--btn {
  display: block;
  align-self: flex-start;
}

.final-cta__background {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
}
.final-cta__background > :nth-child(1) {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1;
}
.final-cta__background > :nth-child(2) {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1;
  background-position: center;
  background-size: cover;
}
.final-cta__background > :nth-child(3) {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.75);
  z-index: 2;
}
.final-cta__background--lower {
  background-position: center;
  background-size: cover;
}
.final-cta__content {
  position: relative;
  z-index: 3;
}
.final-cta #leaves-3 {
  transform: scaleX(-1) translateY(-6rem);
  stroke: white;
}